const ncname = `[a-zA-Z_][\\-\\.0-9_a-zA-Z]*`; // Ê†áÁ≠æÂêç
const qnameCapture = "((?:".concat(ncname, "\\:)?").concat(ncname, ")");
// aa:xx ÂëΩ‰ª§Á©∫Èó¥  Áî®Êù•Ëé∑ÂèñÊ†áÁ≠æÂêç  matchÂêéÁ¥¢Âºï‰∏∫1ÁöÑ‰∏∫ÂåπÈÖçÂà∞ÁöÑÊ†áÁ≠æÂêç   let r =  '<div></div>'.match(qnameCapture);
const startTagOpen = new RegExp("^<".concat(qnameCapture)); // ÂåπÈÖçÂºÄÂßãÊ†áÁ≠æ
const startTagClose = /^\s*(\/?)>/; //   />      <div/>
const defaultTagRE = /\{\{((?:.|\r?\n)+?)\}\}/g; // {{  }}
//  ÂåπÈÖçÂ±ûÊÄß  a=b  a="b"  a='b' ‰∏âÁßçÊÉÖÂÜµ      aa  =  ‚Äú  XXX ‚Äù | ‚Äò  XXX  ‚Äô  |  XXX
const attribute =
  /^\s*([^\s"'<>\/=]+)(?:\s*(=)\s*(?:"([^"]*)"+|'([^']*)'+|([^\s"'=<>`]+)))?/;
const endTag = new RegExp("^<\\/".concat(qnameCapture, "[^>]*>")); // ÂåπÈÖçÈó≠ÂêàÊ†áÁ≠æ </xxxx   >

// htmlparser2 Á¨¨‰∏âÊñπÊèí‰ª∂‰∏éparserHTML‰∏ÄÊ†∑  ÊäähtmlËΩ¨ÂåñÊàêastËØ≠Ê≥ïÊ†ë
export function parserHTML(html) {
  //  ÂâçËøõÂà†Èô§‰ª•ÂåπÈÖçÁöÑÂÜÖÂÆπ
  function advance(len) {
    html = html.substring(len);
  }
  function paserStartTag() {
    const start = html.match(startTagOpen);
    //console.log(start)==>  ['<div', 'div', index: 0, input: '<div id="app"> {{name}}</div>', groups: undefined]
    if (start) {
      const match = {
        tagName: start[1],
        attrs: [],
      };
      advance(start[0].length); //  id="app"> {{name}}</div>

      // ÂåπÈÖçÂ±ûÊÄß
      let end;
      let attr;
      while (
        !(end = html.match(startTagClose)) &&
        (attr = html.match(attribute))
      ) {
        // Ê≤°Âà∞ÂºÄÂßãÊ†áÁ≠æÈó≠Âêà ">" ÂàôËØ¥ÊòéÊúân‰∏™Â±ûÊÄß
        // attr ==>  [' id="app"', 'id', '=', 'app', undefined, undefined, index: 0, input: ' id="app" class="div1">{{name}}</div>', groups: undefined]
        match.attrs.push({
          name: attr[1],
          value: attr[3] || attr[4] || attr[5],
        });
        advance(attr[0].length);
      }
      if (end) {
        advance(end[0].length);
      }
      return match;
    }

    return false; // ‰∏çÊòØÂºÄÂßãÊ†áÁ≠æ
  }
  while (html) {
    let textend = html.indexOf("<"); // ÂΩìÂâçËß£ÊûêÁöÑÂºÄÂ§¥<div id="app">{{name}}</div>
    if (textend == 0) {
      const startTagMatch = paserStartTag();
      // Â¶ÇÊûúÊòØÂºÄÂßãÊ†áÁ≠æ
      if (startTagMatch) {
        start(startTagMatch.tagName, startTagMatch.attrs);
        continue;
      }
      // Â¶ÇÊûúÊòØÁªìÊùüÊ†áÁ≠æ
      const endTagMatch = html.match(endTag);
      if (endTagMatch) {
        end(endTagMatch[1]);
        advance(endTagMatch[0].length);
        continue;
      }
    }
    let text;
    if (textend > 0) {
      //  {{name}}</div>
      text = html.substring(0, textend);
    }
    if (text) {
      chars(text);
      advance(text.length); // </div>
    }
  }
  return root;
}

let root = null;
let stack = []; // Áî®‰∫éÂ≠òÊîæËß£ÊûêÊ†áÁ≠æ   >>>>Ê†à ÂÖàËøõÂêéÂá∫

//  ÂåπÈÖçÂà∞‰∏Ä‰∏™ËäÇÁÇπÔºö Ê†áËÆ∞ËäÇÁÇπÁöÑÁà∂‰∫≤ÊòØË∞Å ÔºåÁà∂‰∫≤ÁöÑÂÑøÂ≠êÊòØÂΩìÊ¨°ÂåπÈÖçÁöÑËäÇÁÇπ ÔºàÂèåÂêëÊ†áËÆ∞Ôºâ
function start(tagName, attributes) {
  let parent = stack[stack.length - 1];
  let element = createAstElement(tagName, attributes);
  if (!root) {
    root = element;
  }
  if (parent) {
    element.parent = parent; // ËÆ∞ÂΩïËäÇÁÇπÁöÑÁà∂‰∫≤
    parent.children.push(element); // ËÆ∞ÂΩïÁà∂‰∫≤ÁöÑÂÑøÂ≠ê
  }
  stack.push(element);
}

function chars(text) {
  text = text.replace(/\s+/g, "");
  if (text) {
    let parent = stack[stack.length - 1];
    parent.children.push({
      type: 3,
      text,
    });
  }
}
function end(tagName) {
  let last = stack.pop();
  if (last.tag != tagName) {
    throw new Error("Ê†áÁ≠æÊ†ºÂºèÊúâËØØ");
  }
}
// Â∞ÜËß£ÊûêÂêéÁöÑÁªìÊûú ÁªÑË£ÖÊàê‰∏Ä‰∏™Ê†ëÁªìÊûÑ -----Ê†à
function createAstElement(tagName, attrs) {
  return {
    tag: tagName,
    type: 1,
    children: [],
    parent: null,
    attrs,
  };
}
