let oldArrayPrototype = Array.prototype;
export let arrayMethods = Object.create(oldArrayPrototype);

let methods = ["push", "shift", "pop", "unshift", "reverse", "splice", "sort"];
methods.forEach((method) => {
  arrayMethods[method] = function (...args) {
    // argsÂèÇÊï∞ÂàóË°®  arr.push(1,2,3)  ...args = [1,2,3]
    // ÊñπÊ≥ïÂä´ÊåÅ Êêû‰∫ãÊÉÖ....
    oldArrayPrototype[method].call(this, ...args);
    //  Â¶ÇÊûúÊï∞ÁªÑÊñπÊ≥ïÊñ∞Ê∑ªÂä†ÁöÑÂÄºÊòØ‰∏Ä‰∏™ÂØπË±° ÂàôÂêåÊ†∑ÈúÄË¶ÅËøõË°åËßÇÊµã
    let inserted; // Êñ∞Ê∑ªÂä†ÁöÑÂÄº
    switch (method) {
      case "push":
      case "unshift":
        inserted = args; // Á±ªÊï∞ÁªÑ
        break;
      case "splice":
        inserted = args.slice(2);
        break;
    }
    let ob = this.__ob__;
    if (inserted) ob.observeArray(inserted);
    ob.dep.notify();
    //  todo Êõ¥Êñ∞Êìç‰Ωú
  };
});

