export function patch(oldVnode, vnode) {
  if (oldVnode.nodeType == 1) {
    let parentElm = oldVnode.parentNode;
    let elm = createElm(vnode); // Ê†πÊçÆËôöÊãüËäÇÁÇπÂàõÂª∫ÁúüÂÆûDOM
    parentElm.insertBefore(elm, oldVnode.nextSibling);
    parentElm.removeChild(oldVnode);
    return elm;
    // Á¨¨‰∏ÄÊ¨°Ê∏≤ÊüìÂêé Âà†Èô§‰∫Üvm.$el ÊâÄ‰ª•ÊúÄÂêéË¶ÅËøîÂõûÊñ∞ÁöÑelm
  }
}
// Ê†∏ÂøÉÊñπÊ≥ï createElmÊñπÊ≥ï  Â∞ÜËôöÊãüDOMËΩ¨Êç¢ÊàêÁúüÂÆûDOM
function createElm(vnode) {
  let { tag, data, children, text, vm } = vnode;
  if (typeof tag == "string") {
    // ÂÖÉÁ¥†
    vnode.el = document.createElement(tag); // ËôöÊãüËäÇÁÇπvnodeÊúâ‰∏Ä‰∏™elÂ±ûÊÄßÂØπÂ∫îÁúüÂÆûËäÇÁÇπ
    children.forEach((child) => {
      vnode.el.appendChild(createElm(child));
    });
  } else {
    // ÊñáÊú¨
    vnode.el = document.createTextNode(text);
  }
  return vnode.el;
}
