export function isfn(fn) {
  return typeof fn === "function";
}

export function isObject(obj) {
  return typeof obj == "object" && obj != null;
}

let callbacks = [];
function flushCallback() {
  callbacks.forEach((cb) => {
    cb();
  });
  callbacks = [];
  waiting = false;
}
function timer(flushCallback) {
  let timerFn = () => {};
  if (Promise) {
    // ÂæÆ‰ªªÂä°
    timerFn = () => {
      Promise.resolve().then(flushCallback);
    };
  } else if (MutationObserver) {
    // ÂæÆ‰ªªÂä°
    let textNode = document.createTextNode(1);
    let observe = new MutationObserver(flushCallback);
    observe.observe(textNode, { characterData: true });
    timerFn = () => {
      textNode.textContent = 3;
    };
  } else if (setImmediate) {
    timerFn = () => {
      setImmediate(flushCallback);
    };
  } else {
    timerFn = () => {
      setTimeout(flushCallback, 0);
    };
  }
  timerFn();
}

// ÂÜÖÈÉ®ÂÖàË∞ÉÁî®nextick ÔºöflushSchedulerQueue
//Áî®Êà∑ÂêéË∞Énextick vm.$nextick(function(){console.log( vm.$el)});

// ÂÜÖÈÉ®ÂíåÁî®Êà∑ÂêÑ‰∏ÄÂÖ±Ë∞ÉÁî®2Ê¨°nextTickÔºåÂÖ∂ÂÆûÊõ¥Êñ∞ËßÜÂõæÈÄªËæëÂè™ÈúÄÊâßË°å‰∏ÄÊ¨°  Èò≤ÊäñÂ§ÑÁêÜ
let waiting = false;
export function nextick(cb) {
  callbacks.push(cb);
  if (!waiting) {
    timer(flushCallback);
    waiting = true;
  }
}
