import { compileToFunction } from "./compiler/index";
import { mountComponent } from "./lifecycle";
import { initState } from "./state";
// initMixinÂáΩÊï∞Áî®‰∫éÂú®vueÂéüÂûãÊ∑ªÂä†initÂàùÂßãÂåñÊñπÊ≥ï
export function initMixin(Vue) {
  Vue.prototype._init = function (options) {
    const vm = this;
    vm.$options = options;
    // ÂØπÊï∞ÊçÆËøõË°åÂàùÂßãÂåñ data el methods computed props
    initState(vm);
    // Ê®°ÊùøÁºñËØë
    if (vm.$options.el) {
      vm.$mount(vm.$options.el);
    }
  };
  //  Â∞ÜÊï∞ÊçÆÊåÇËΩΩÂà∞Ê®°Êùø‰∏ä
  Vue.prototype.$mount = function (el) {
    const vm = this;
    const options = vm.$options;
    el = document.querySelector(el);
    vm.$el = el;
    //  1 ÊääÊ®°ÊùøËΩ¨ÂåñÊàêÂØπÂ∫îÁöÑÊ∏≤ÊüìÂáΩÊï∞===> ËôöÊãüDOMÊ¶ÇÂøµ vnode====> diffÁÆóÊ≥ïÊõ¥Êñ∞ËôöÊãüDOM====>‰∫ßÁîüÁúüÂÆûËäÇÁÇπ Êõ¥Êñ∞
    if (!options.render) {
      let template = options.template;
      if (!template && el) {
        template = el.outerHTML;
        let render = compileToFunction(template);
        options.render = render;
        // options.renderÂáΩÊï∞Â∞±ÊòØÊ∏≤ÊüìÂáΩÊï∞,Êé•‰∏ãÊù• Ë∞ÉÁî®renderÊñπÊ≥ïÔºåÊ∏≤ÊüìÊàêÁúüÂÆûDOMÔºåÊõøÊç¢ÊéâÈ°µÈù¢ÂÜÖÂÆπ
        // Â∞ÜÁªÑ‰ª∂vmÂÆû‰æãÊåÇËΩΩÂà∞el‰∏ä---ÁªÑ‰ª∂ÁöÑÊåÇËΩΩÊµÅÁ®ã
        mountComponent(vm, el);
      }
    }
  };
}
