import { observe } from "./observer/index";
import { Watcher } from "./observer/watcher";
import { isfn } from "./utils";

export function stateMixin(Vue) {
  Vue.prototype.$watch = function (key, handler, options = {}) {
    options.user = true; // Ë°®Á§∫ËøôÊòØÁî®Êà∑Ëá™Â∑±ÂÜôÁöÑwatcher  ÈùûÊ∏≤Êüìwatcher
    new Watcher(this, key, handler, options);
  };
}
// Êï∞ÊçÆÁä∂ÊÄÅÂàùÂßãÂåñ
export function initState(vm) {
  const opts = vm.$options;
  if (opts.data) {
    initData(vm);
  }
  if (opts.watch) {
    initWatch(vm, opts.watch);
  }
  if (opts.computed) {
    initComputed(vm, opts.computed);
  }
}
function initData(vm) {
  let data = vm.$options.data;
  // Ê≠§Êó∂dataÂíåvmÊ≤°Êúâ‰ªª‰ΩïÂÖ≥Á≥ª  data = isfn(data) ? data.call(vm) : data;  Ëß£ÂÜ≥Ôºö
  data = vm._data = isfn(data) ? data.call(vm) : data; // Âà§Êñ≠dataÊòØÂáΩÊï∞ËøòÊòØjsonÂØπË±°
  // vm.name ----> vm._data.name
  for (let key in data) {
    proxy(vm, "_data", key);
  }
  observe(data);
}
function initWatch(vm, watch) {
  for (let key in watch) {
    let handler = watch[key];
    if (Array.isArray(handler)) {
      for (let i = 0; i < handler.length; i++) {
        createWatcher(vm, key, handler[i]);
      }
    } else {
      createWatcher(vm, key, handler);
    }
  }
}

function createWatcher(vm, key, handler) {
  return vm.$watch(key, handler);
}

function initComputed(vm, computed) {
  for (let key in computed) {
    const userDef = computed[key]; // ÂèØËÉΩÊòØÂØπË±°Ôºå‰πüÂèØËÉΩÊòØÂáΩÊï∞
    let getter = typeof userDef == "function" ? userDef : userDef.get;
    // ÊúâÂ§öÂ∞ë‰∏™getterÂ∞±ÂàõÂª∫Â§öÂ∞ë‰∏™watcher Ôºå ÊØè‰∏™ËÆ°ÁÆóÂ±ûÊÄßÁöÑÊú¨Ë¥®Â∞±ÊòØwatcher
    new Watcher(vm, getter, () => {}, { lazy: true }); // lazy:true ËÆ©ÂÖ∂‰∏çÈ©¨‰∏äÊâßË°åÔºå Âè™ÊúâÂΩìËÆ°ÁÆóÂ±ûÊÄßË¢´Ë∞ÉÁî®ÂêéÊâçÊâßË°å

    // Â∞ÜkeyÂÆö‰πâÂú®vmÁöÑ_data‰∏ä   vm_data.fullname  ËÆ°ÁÆóÂ±ûÊÄßÁöÑÊú¨Ë¥®‰πüÊòØ‰ΩøÁî® Object.defineProperty
    defineComputed(vm, key, userDef);
  }
}
// Êï∞ÊçÆ‰ª£ÁêÜ  ÂΩìË∞ÉÁî®vm.name ÂàôÂú® vm._data.nameËé∑Âèñ
function proxy(vm, source, key) {
  Object.defineProperty(vm, key, {
    get() {
      return vm[source][key];
    },
    set(newValue) {
      vm[source][key] = newValue;
    },
  });
}
