import { popTarget, pushTarget } from "./dep";
import { queueWatcher } from "./scheduler";
let id = 0;
export class Watcher {
  constructor(vm, exprOrFn, cb, options) {
    this.vm = vm;
    this.exprOrFn = exprOrFn;
    this.cb = cb;
    this.options = options;
    this.user = !!options.user; // ÊòØÂê¶ÊòØÁî®Êà∑watcher  watch
    this.deps = []; // Â≠òÊîædep
    this.depsId = new Set();
    // ‰∏çËÆ∫ÊòØÊ∏≤ÊüìËøòÊòØwatchÁõëÂê¨ÁöÑwatcherÔºå this.getterÊñπÊ≥ïÁöÑ‰ΩúÁî®ÈÉΩÊòØÂèñÂÄºÊìç‰ΩúÔºåËß¶ÂèëÂ±ûÊÄßÁöÑgetËøõË°å‰æùËµñÊî∂ÈõÜ
    if (typeof exprOrFn === "string") {
      this.getter = function () {
        // ÂΩìÂèñÂÄºÊó∂ÔºåÂ∞±‰ºöËøõË°å‰æùËµñüì±
        let path = exprOrFn.split("."); // 'person.name' ==> [person, name]
        let obj = vm;
        for (let i = 0; i < path.length; i++) {
          obj = obj[path[i]];
        }
        return obj;
      };
    } else {
      // ÈªòËÆ§ËÆ© exprOrFnÊâßË°å  Ë∞ÉÁî®‰∫ÜrenderÊñπÊ≥ïÂéªvm‰∏äÂèñÂÄº
      this.getter = exprOrFn;
    }

    this.value = this.get();
    this.id = id++; // ÊØè‰∏™ÂÆû‰æãwatcherÂîØ‰∏ÄÊ†áËØÜ
  }
  // Êï∞ÊçÆÊõ¥Êñ∞Êó∂Ôºå Ë∞ÉÁî®get
  get() {
    // ÂΩìË∞ÉÁî®get‰ºöÂéªvm‰∏äÂèñÂÄº ‰ºöËß¶ÂèëdefinedProperty.get
    // ÊØè‰∏™Â±ûÊÄßÈÉΩÂèØ‰ª•Êî∂ÈõÜËá™Â∑±ÁöÑwatcher---Êî∂ÈõÜ‰æùËµñdep
    //‰∏Ä‰∏™Â±ûÊÄßÂØπÂ∫îÂ§ö‰∏™watcher   ‰∏Ä‰∏™watcherÂèØ‰ª•ÂØπÂ∫îÂ§ö‰∏™Â±ûÊÄß
    pushTarget(this); // Dep.target = watcher
    const value = this.getter();
    popTarget(); // Dep.target = null , Â¶ÇÊûúDep.targetÊúâÂÄº ÂàôËØ¥ÊòéËøô‰∏™ÂèòÈáèÂú®Ê®°Êùø‰∏≠Ë¢´‰ΩøÁî®
    return value;
  }
  update() {
    // vue‰∏≠ÁöÑÊõ¥Êñ∞ÊòØÂºÇÊ≠•ÁöÑ   Â§öÊ¨°Ë∞ÉÁî®updateÔºåÂÖàÂ∞ÜwatcherÂ≠òÊîæËµ∑Êù•ÔºåÁ≠â‰ºö‰∏ÄËµ∑Êõ¥Êñ∞
    // this.get();
    queueWatcher(this);
    // ÂºÇÊ≠•Êõ¥Êñ∞ÈòüÂàó https://v2.cn.vuejs.org/v2/guide/reactivity.html#%E5%BC%82%E6%AD%A5%E6%9B%B4%E6%96%B0%E9%98%9F%E5%88%97
  }
  run() {
    let newValue = this.get();
    let oldValue = this.value;
    this.value = newValue;
    if (this.user) {
      // ÂøÖÈ°ªÊòØÁî®Êà∑watcherÊâçË∞ÉÁî®
      this.cb.call(this.vm, oldValue, newValue);
    }
  }
  addDep(dep) {
    if (!this.depsId.has(dep.id)) {
      this.depsId.add(dep.id);
      this.deps.push(dep);
      dep.addSub(this);
    }
  }
}

/**
 * ÊÄùË∑ØÔºöwatcherÂíådep
 * 1„ÄÅÊàë‰ª¨Â∞ÜÊõ¥Êñ∞ÁöÑÂäüËÉΩÂ∞ÅË£ÖÊàê‰∫Ü‰∏Ä‰∏™watcher
 * 2„ÄÅÊ∏≤ÊüìÈ°µÈù¢Êó∂Ôºå‰ºöÂ∞ÜÂΩìÂâçwatcherÊîæÂà∞ Dep.target‰∏ä
 * 3„ÄÅÂú®vue‰∏≠È°µÈù¢Ê∏≤Êüì‰ΩøÁî®ÁöÑÂ±ûÊÄßÔºåÈúÄË¶ÅËøõË°å‰æùËµñÊî∂ÈõÜÔºåÊî∂ÈõÜÂØπË±°ÁöÑÊ∏≤Êüìwatcher
 * 4„ÄÅÂèñÂÄºÊó∂ÔºåÁªôÊØè‰∏™Â±ûÊÄßÈÉΩÂä†‰∏ädepÂ±ûÊÄßÔºå Áî®‰∫éÂ≠òÊîæËøô‰∏™Ê∏≤Êüìwatcher ÔºàÂêå‰∏Ä‰∏™watcher‰ºöÂØπÂ∫îÂ§ö‰∏™depÔºâ
 * 5„ÄÅÊØè‰∏™Â±ûÊÄßÂèØËÉΩÂØπÂ∫îÂ§ö‰∏™ËßÜÂõæÔºàÂ§ö‰∏™ËßÜÂõæÂèØËÉΩÊòØÂ§ö‰∏™watcherÔºâÔºå‰∏Ä‰∏™Â±ûÊÄßË¶ÅÂØπÂ∫îÂ§ö‰∏™watcher
 * 6„ÄÅdep.depend(); ÈÄöÁü•depÂ≠òÊîæwatcher    Dep.target.addDep() ÈÄöÁü•watcherÂ≠òÊîædep
 * ÂèåÂêëÂ≠òÂÇ®
 *
 *
 *
 * ÊØè‰∏™Â±ûÊÄßÂàÜÈÖç‰∏Ä‰∏™watcher(‰ΩøÁî®pushTargetÊñπÊ≥ï Â∞ÜwatherÊîæÂà∞DepÁöÑtargetÂ±ûÊÄß‰∏ä )Áî®‰∫éÊî∂ÈõÜÂì™‰∫õÁªÑ‰ª∂‰æùËµñ‰∫éÊ≠§Â±ûÊÄßÔºå
 * ÂΩìÊï∞ÊçÆÂèëÁîüÊîπÂèòÊó∂ÔºåËÆ©Â±ûÊÄßDep‰∏≠ÊâÄÊî∂ÈõÜÁöÑwatcherÊõ¥Êñ∞ÂØπÂ∫îÁöÑÁªÑ‰ª∂ËßÜÂõæ
 *
 * */
