import { nextick } from "../utils";
// Ë∞ÉÂ∫¶
let queue = []; // Â≠òÊîæÊõ¥Êñ∞ËßÜÂõæÁöÑwatcher ÂéªÈáç
let has = {};
function flushSchedulerQueue() {
  for (var i = 0; i < queue.length; i++) {
    queue[i].run();
  }
  queue = [];
  has = {};
  pending = false;
}
let pending = false;
export function queueWatcher(watcher) {
  let id = watcher.id;
  if (has[id] == null) {
    queue.push(watcher);
    has[id] = true;
    // ÂºÄÂêØ‰∏ÄÊ¨°Êõ¥Êñ∞Êìç‰ΩúÔºå ÊâπÂ§ÑÁêÜ ÔºàÈò≤ÊäñÔºâ
    if (!pending) {
      nextick(flushSchedulerQueue); // Á≠âÂæÖÊâÄÊúâÂêåÊ≠•ÊâßË°åÂÆåÔºåÂÜçÊâßË°åÊõ¥Êñ∞Êìç‰Ωú
      pending = true;
    }
  }
}
// ÂêåÊ≠•‰ª£Á†ÅÊâßË°åÂÆåÂêé„ÄÇÊâßË°åÊ†à‰∏≠ÂÖàÊâßË°åÂæÆ‰ªªÂä°ÔºàÔºâÔºåÂú®ÊâßË°åÂÆè‰ªªÂä°ÔºåÂΩìÂêåÊ≠•Êï∞ÊçÆÊõ¥ÊîπÂêéÔºåÊàë‰ª¨Â∏åÊúõÂ∞ΩÂø´Êõ¥Êñ∞ËßÜÂõæ ÊâÄ‰ª•‰ΩøÁî®ÂæÆ‰ªªÂä°
// ÂÆöÊó∂Âô®ÊòØÂÆè‰ªªÂä°ÔºåÊâÄ‰ª•ËÄÉËôë‰ΩøÁî®promiseÂæÆ‰ªªÂä° vueÂÜÖÈÉ®Â∞ÅË£Ö‰∫Ü‰∏Ä‰∏™nextickÊñπÊ≥ï‰ΩøÁî®promise
