// ‰æùËµñÊî∂ÈõÜ  ÊØè‰∏™Â±ûÊÄßÈÉΩÂàÜÈÖç‰∏Ä‰∏™Dep  DepÂèØ‰ª•Áî®Êù•Â≠òÊîæwatcher ÂèçËøáÊù• watcher‰∏≠‰πüË¶ÅÂ≠òÊîædep
let id = 0;
export class Dep {
  constructor() {
    this.id = id++;
    this.subs = []; // Áî®Êù•Â≠òÊîæwatcher
  }
  // Â∞ÜdepÂ≠òÊîæÂà∞watcher‰∏≠
  // dep‰∏≠Ë¶ÅÂ≠òÊîæwatcher  watcher‰∏≠‰πüË¶ÅÂ≠òÊîædep Â§öÂØπÂ§ö
  depend() {
    if (Dep.target) {
      Dep.target.addDep(this);
    }
  }
  // Â∞ÜwatcherÂ≠òÊîæÂà∞dep‰∏≠
  addSub(watcher) {
    this.subs.push(watcher);
  }
  //  ÈÄöÁü•ÊâÄÊúâwatcherÊõ¥Êñ∞ËßÜÂõæ
  notify() {
    this.subs.forEach((watcher) => {
      watcher.update();
    });
  }
}
Dep.target = null;
export function pushTarget(watcher) {
  Dep.target = watcher;
}
export function popTarget() {
  Dep.target = null;
}
