import { arrayMethods } from "./array";
import { isObject } from "../utils";
import { Dep } from "./dep";
//Êï∞ÊçÆËßÇÂØüËÄÖÊ®°Âºè
export function observe(data) {
  // vue2‰∏≠‰ºöÂ∞ÜÊâÄÊúâÁöÑdataÊï∞ÊçÆËøõË°åÊï∞ÊçÆÂä´ÊåÅÔºü‰ªÄ‰πàÊòØÊï∞ÊçÆÂä´ÊåÅÔºü Object.defineproperty
  // Â¶ÇÊûúÊòØÂØπË±°ÊâçËøõË°åËßÇÊµã
  if (!isObject(data)) {
    return;
  }
  if (data.__ob__) {
    return data.__ob__; // Â¶ÇÊûúÊï∞ÊçÆÊúâ__ob__ÂàôËØ¥ÊòéÊï∞ÊçÆÂ∑≤ÁªèË¢´ËßÇÊµã‰∫ÜÔºåÂàôÊó†ÈúÄËßÇÊµã
  }
  return new Observer(data); // ËøõË°åËßÇÊµã  ÈªòËÆ§ÊúÄÂ§ñÂ±ÇÁöÑdataÂøÖÈ°ªÊòØ‰∏Ä‰∏™ÂØπË±°
}
// Â¶ÇÊûúÁªôÂØπË±°Êñ∞Â¢û‰∏Ä‰∏™Â±ûÊÄßÔºå‰∏ç‰ºöËß¶ÂèëËßÜÂõæÊõ¥Êñ∞ÔºàÁªôÂØπË±°Êú¨Ë∫´‰πüÂ¢ûÂä†‰∏Ä‰∏™depÔºàdep‰∏≠Â≠òwatcherÔºâÔºå Â¢ûÂä†‰∏Ä‰∏™Â±ûÊÄßÂêéÔºåÊâãÂä®Ëß¶ÂèëwatcherÊõ¥Êñ∞Ôºâ
class Observer {
  // Ê£ÄÊµãÊï∞ÊçÆÁöÑÂèòÂåñ
  constructor(data) {
    // ÂØπÂØπË±°‰∏≠ÁöÑÊâÄÊúâÂ±ûÊÄßËøõË°åÂä´ÊåÅ
    // data.__ob__ = this;  Â∞ÜËßÇÊµãËÄÖÂÆû‰æãÊåÇËΩΩÂà∞ËßÇÊµãÁöÑdataÊï∞ÊçÆ‰∏ä, ‰∏çËÉΩÁõ¥Êé•Ê∑ªÂä†Ôºå‰ºöÈÄíÂΩíobserve
    this.dep = new Dep(); // arr._ob_.dep    Êï∞ÊçÆÂèØËÉΩÊòØÂØπË±° ‰πüÂèØËÉΩÊòØÊï∞ÁªÑ  Êï∞ÁªÑÊÄé‰πàÊî∂ÈõÜ‰æùËµñÔºü  Âú®È°µÈù¢ {{ arr}} ‰ºöËß¶Âèë obj={ arr:[1,2,3]} objÁöÑget
    Object.defineProperty(data, "__ob__", {
      value: this,
      enumerable: false,
    });
    if (Array.isArray(data)) {
      // Êï∞ÊçÆÂä´ÊåÅÁöÑÈÄªËæë  Âä´ÊåÅÊï∞ÁªÑÁöÑÂèòÂºÇÊñπÊ≥ïÔºàÂèòÂºÇÊñπÊ≥ï:ÊåáÊìç‰ΩúÊñπÊ≥ïÂèØËÉΩ‰ºöÊîπÂèòÂéüÊï∞ÁªÑÂ¶ÇÔºö push shift pop ËÄåcontact‰∏çÊòØÂèòÂºÇÊñπÊ≥ïÔºåÂõ†ÂÖ∂‰∏ç‰ºöÊîπÂèòÂéüÊï∞ÁªÑÔºâ
      // ÂØπÊï∞ÁªÑÂéüÊù•ÁöÑÊñπÊ≥ïËøõË°åÊîπÂÜôÔºàÈáçÂÜôÔºâ----> ÂàáÁâáÁºñÁ®ã È´òÈò∂ÂáΩÊï∞
      data.__proto__ = arrayMethods;
      this.observeArray(data); // Â¶ÇÊûúÊï∞ÁªÑ‰∏≠ÁöÑÊï∞ÊçÆÊòØÂØπË±°ÔºåÂàôÈúÄË¶ÅÂØπÂØπË±°ËøõË°åÂä´ÊåÅ [{key:value}, {key:value}]
    } else {
      this.walk(data); // ÂØπË±°Âä´ÊåÅÁöÑÈÄªËæë
    }
  }
  observeArray(data) {
    data.forEach((item) => {
      observe(item);
    });
  }
  // Âä´ÊåÅÂØπË±°
  walk(data) {
    // dataÊï∞ÊçÆÂØπË±°
    Object.keys(data).forEach((key) => {
      defineReactive(data, key, data[key]);
    });
  }
}
function dependArray(value) {
  for (let i = 0; i < value.length; i++) {
    let current = value[i]; // current ÊòØÊï∞ÁªÑ‰∏≠ÁöÑÊï∞ÁªÑ
    current.__ob__ && current.__ob__.dep.depend();
    if (Array.isArray(current)) dependArray(current);
  }
}
// vue2‰ºöÂØπÂØπË±°ËøõË°åÈÅçÂéÜ(ÈÄíÂΩí)Ôºå Â∞ÜÊØè‰∏™ÂØπË±°Áî® Object.definePropertyÈáçÊñ∞ÂÆö‰πâÔºå ÊÄßËÉΩÂ∑Æ!!!
function defineReactive(data, key, value) {
  let childOb = observe(value); // Â¶ÇÊûúÂØπË±°Â±ûÊÄßÁöÑÂÄº‰∏∫ÂØπË±°ÔºåÂàôÈÄíÂΩíÂä´ÊåÅÂØπË±°Â±ûÊÄß ÔºåÊâÄ‰ª•Âú®‰ΩøÁî®Vue2ÁöÑÊó∂ÂÄôÔºåÂ∞ΩÈáèÂ∞ÜÊï∞ÊçÆÊâÅÂπ≥Âåñ‰∏çË¶ÅËøáÂ§öÂµåÂ•ó
  let dep = new Dep(); // ÊØè‰∏™Â±ûÊÄßÈÉΩÊúâ‰∏Ä‰∏™dep

  Object.defineProperty(data, key, {
    get() {
      // ÂΩìË∞ÉÁî®Â±ûÊÄßÊó∂ÔºåÂ∏åÊúõÂ∞ÜdepÂíåwatcherÂÖ≥ËÅî‰∏ä
      // Â¶ÇÊûúDep.targetÊúâÂÄº ÂàôÊ≠§ÂÄºÊòØÂú®Ê®°Êùø‰∏≠ÂèñÂÄºÁöÑ
      if (Dep.target) {
        dep.depend();
        if (childOb) childOb.dep.depend(); // childObÂèØËÉΩÊòØÊï∞ÁªÑ ‰πüÂèØËÉΩÊòØÂØπË±°  ÊòØÂØπË±°ÂêéÁª≠ÂºÄÂèë$setÊó∂ ‰ºö‰πü‰ºöËÆ©ÂØπË±°Êõ¥Êñ∞ËßÜÂõæ
        if (Array.isArray(value)) {
          dependArray(value);
        }
      }
      return value;
    },
    set(newValue) {
      // todo Áî®Êà∑Êõ¥Êîπ‰∫ÜÊï∞ÊçÆ.....
      if (newValue !== value) {
        observe(newValue); // Â¶ÇÊûúÂØπË±°ÁöÑÊüê‰∏™Â±ûÊÄßÈáçÊñ∞ËµãÁöÑÂÄº‰πüÊòØ‰∏Ä‰∏™ÂØπË±°ÔºåÂàô‰πüÈúÄË¶ÅË¢´Âä´ÊåÅ„ÄÇ
        value = newValue;
        dep.notify(); // ÂëäËØâÂΩìÂâçÂ±ûÊÄßÁöÑDepÂ≠òÊîæÁöÑwatcherÊâßË°åÊõ¥Êñ∞
      }
    },
  });
}

/**
 * ÈóÆÈ¢òÔºö ÁªôdataÊ∑ªÂä†Â±ûÊÄß‰ºöË¢´Âä´ÊåÅÂà∞ÂêóÔºü
 *ÊÄªÁªì Ôºö Â¶ÇÊûúÊòØÂØπË±°Ôºå‰ºöÂØπÂØπË±°ËøõË°åÈÄíÂΩíÂä´ÊåÅ
         Â¶ÇÊûúÊòØÊï∞ÁªÑ ‰ºöÂä´ÊåÅÊï∞ÁªÑÁöÑÊñπÊ≥ïÔºåÂπ∂ÂØπÊï∞ÁªÑ‰∏≠‰∏çÊòØÂü∫Êú¨Êï∞ÊçÆÁ±ªÂûãÁöÑÊï∞ÊçÆËøõË°åÊ£ÄÊµã 
 * */
